@import "variables.less";

.modal {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: black;
  background: rgba(0,0,0,0.6) url('/images/bg.png') left top repeat;
}

.modal .wrapper {
  width: 320px;
  padding: 55px;
  height: auto;
  min-height: 200px;
  margin: 160px auto 0 auto;
  background: white url('/images/bg.png') left top repeat;
  position: relative;
}

.modal .close {
  position: absolute;
  display: block;
  right: -13px;
  top: -13px;
  width: 23px;
  height: 23px;
  background: white url('/images/x-active.png') left top no-repeat;
  border: 2px solid @accent_color;
  cursor: pointer;
}